import React from 'react';
import styles from './users.module.css';
import userPhoto from '../../assets/images/user.png';
import { NavLink } from 'react-router-dom';

let User = ({ user, followingInProgress, unfollow, follow }) => {
    return (
        <div>
            <span>
                <div>
                    <NavLink to={'/profile/' + user.id}>  {/* куда ведёт */}
                        <img src={user.photos.small != null ? user.photos.small : userPhoto}
                            // если фото не равен null отоброзить, иначе отобразить фото по умолчанию
                            className={styles.userPhoto} />
                    </NavLink>
                </div>
                <div>
                    {user.followed      // если подписаны
                        // при клике на мышь отработает колбэк и возьмет в пропсах unfollow и передаст id
                        ? <button disabled={followingInProgress.some(id => id === user.id)}
                            onClick={() => { unfollow(user.id) }}>
                            Unfollow</button>
                        : <button disabled={followingInProgress.some(id => id === user.id)}
                            onClick={() => { follow(user.id) }}>
                            Follow</button>}
                </div>
            </span>
            <span>
                <span>
                    <div>{user.name}</div>
                    <div>{user.status}</div>
                </span>
                <span>
                    <div>{'user.location.country'}</div>
                    <div>{'user.location.city'}</div>
                </span>
            </span>
        </div>)
}
export default User; 